.login__input {
  margin: 9px 34px 0 34px;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, .2) ;
  padding-bottom: 10px;
  outline: none;
  font-family: 'Inter';
  font-size: 14px;
  line-height: 17px;
}

.login__input::placeholder {
  color: rgba(0, 0, 0, .2);
}

@media screen and (max-width: 425px) {
  .login__input {
    margin-left: 16px;
    margin-right: 16px;
  }
}